import React, { Component } from 'react'  
import FacebookLogin from 'react-facebook-login';  
export class Logintbyfacebook extends Component {  
        constructor(props) {  
                super(props);  
                this.state = {  
              
                };  
        }  
        
        signup(res) {  
          const responseFacebook = {  
            Name: res.name,  
            email: res.email,  
            token: res.accessToken,  
            Image: res.picture.data.url,  
            ProviderId: 'Facebook'  
        
          }  
          
          sessionStorage.setItem("userData", JSON.stringify(responseFacebook));  
          this.props.history.push('/Dashboard')

        };  
        render() {  
            const facebookAppId = process.env.REACT_APP_FACEBOOK_APP_ID;
            const responseFacebook = (response) => {  
                    console.log(response);  
                 /*    var res = response.profileObj;  
                    console.log(res);  */ 
                    debugger;  
                    this.signup(response);  
                  }  
            return (  
                    <div className="App">  
                    {/* <div className="row">  
                      <div className="col-sm-12 btn btn-info">  
                        Login With Facebook Using ReactJS  
                        </div>  
                    </div>   */}
                    <div className="row">  
                      <div style={{ 'paddingTop': "20px" }} className="col-sm-12">  
                        <div className="col-sm-4"></div>  
                        <div className="col-sm-4">  
                        <FacebookLogin buttonStyle={{padding:"6px"}}  
                          appId={facebookAppId}  
                          autoLoad={true}
                          version="10.0" 
                          fields="name,email,picture"  
                          callback={responseFacebook}/>  
                        </div>  
                        <div className="col-sm-4"></div>  
                      </div>  
                    </div>  
                  </div>  
            )  
        }  
}  
  
export default Logintbyfacebook