import React, { useEffect, useState, useRef } from 'react'
import InfiniteScroll from "react-infinite-scroll-component";
import {
  Paper,
  makeStyles,
  Grid,
  Avatar,
  Typography,
  TextField,
  Link,
  Chip,
  IconButton,
  Popover
} from "@material-ui/core";
import SendIcon from "@material-ui/icons/Send";
import DoneIcon from "@material-ui/icons/Done";
import SearchIcon from '@material-ui/icons/Search';

// the main page with most of the logic
function Dashboard() {
  const [name, setName] = useState(() => {
    return JSON.parse(sessionStorage.getItem('userData'));
  });
// 2 state variables
  const [posts, setPosts] = React.useState({
                          data: [],
                          hasMore: true,
                          nextPage: "first",
                          first: true
                          });
  const [searchTopic, setSearchTopic] = React.useState("");
 // useRef is a special state variable but doesn't cause re-render on update 
  const countRef = useRef(0);
 
  // for popover handle
  const [anchorEl, setAnchorEl] = React.useState(null);
  /* const handlePopClick = (event) => {  //see handlPost
    setAnchorEl(event.currentTarget);
  }; */
  const handlePopClose = () => {
    setAnchorEl(null);
  };
  const open = Boolean(anchorEl);
  const id = open ? 'simple-popover' : undefined;
// end popHanlde

  useEffect(() => {
    countRef.current++;
    console.log(`in useEffect ${countRef.current}`);
    if (posts.first) {
      const facebookGroupId = process.env.REACT_APP_FACEBOOK_GROUP_ID;
      window.FB.api('/' + facebookGroupId + '/feed',
        'GET',
        { "fields": "from,id,message,updated_time,comments.limit(100).filter(stream){created_time,from,message,id,like_count,comment_count},reactions,full_picture" }
        , addPosts);
    }

  });

  const useStyles = makeStyles((theme) => ({
    root: {
      flexGrow: 1,
      overflow: "hidden",
      background: "linear-gradient(45deg, #fafafa 30%, #e3f2fd 90%)",
    },
    paper: {
      maxWidth: 500,
      margin: `${theme.spacing(1)}px auto`,
      padding: theme.spacing(2)
    },
    textField: {
      marginLeft: theme.spacing(1),
    },
    typography: {
      padding: theme.spacing(2),
    },
    form: {
      padding: theme.spacing(0, 3),
    },
    cftd: {
      backgroundColor: "#FF4500",
      height: "20px",
      width: "35px",
      color: "#FFFFFF"
    },
    iconButton: {
      padding: 10
    },
    outerColumn: {
      borderRight: "1px solid grey",
      borderBottom: "1px solid grey",
      borderLeft: "1px solid grey"
    },
    centerColumn: {
      marginLeft: theme.spacing(1)
    }
  }));

 // adds the respose to the state 'posts' after FB.API call
  const addPosts = (response) => {
    console.log(response);
    /* if the response has no data, just update posts.hasMore to false and return. Thre must be a simpler way, but I am just copying the entire state just to update one node.
    Similar method used few times in this class */  
    if (response.data.length === 0) {
      let postsCopy = Object.assign({}, posts);
      postsCopy.hasMore = false;
      setPosts(postsCopy);
      return;
    }
    const regexCftd = new RegExp(/#CFTD/i);
    const regexTopic = new RegExp(searchTopic, 'i');
    let selectCount = 0;
    let postsCopy = Object.assign({}, posts);

    response.data.forEach((dta, index, array) => {

      let cftdPost = regexCftd.test(dta.message);
      let aPost = {
        from: dta.from ? dta.from : { name: "Unknown", id: "9999999999999999" },
        id: dta.id,
        message: dta.message,
        updated_time: dta.updated_time,
        comments: dta.comments ? dta.comments.data.length : 0,
        reactions: dta.reactions ? dta.reactions.data.length : 0,
        link: "https://www.facebook.com/groups/cryptics/posts/" + dta.id.substr(16) + "/",
        full_picture: dta.full_picture ? dta.full_picture : "",
        cftd: cftdPost,
        // from localStorage
        solve: localStorage.getItem(dta.id) ? JSON.parse(localStorage.getItem(dta.id))
          : { ans: "", anno: "", solved: false },
      };
      /*  messed up a bit trying to cater for 2 possible scenarios in one function, need clean up at some stage
      1. searchTopic null => select all posts and if cftd, select all comments of cftd post
      2. searchTopic !null  => select posts that matches searchTopic 
                          if post doesn't match, check in all comments to the post */

      if (searchTopic === "") {       //scenario 1
        console.log(`Selected ${selectCount++}`);
        postsCopy.data.push(aPost);

        if (cftdPost && (aPost.comments > 0)) {
          dta.comments.data.forEach(function (dtc, index, array) {
            let cPost = {
              from: dtc.from ? dtc.from : { name: "Unknown", id: "9999999999999999" },
              id: dtc.id,
              message: dtc.message,
              updated_time: dtc.created_time,
              comments: dtc.comment_count,
                reactions: dtc.like_count,
              link: "https://www.facebook.com/groups/cryptics/posts/" + dta.id.substr(16) + "/",
              cftd: true,
              solve: localStorage.getItem(dtc.id) ? JSON.parse(localStorage.getItem(dtc.id))
                : { ans: "", anno: "", solved: false },
            };
            console.log(`Selected ${selectCount++}`);
            postsCopy.data.push(cPost);
          });
        }
      } else {           //scenario 2
        let selectPost = regexTopic.test(aPost.message) || regexTopic.test(aPost.from.name);
        let cftdPost = regexCftd.test(dta.message);
        if (selectPost) {
          console.log(`Selected ${selectCount++}`);
          postsCopy.data.push(aPost);
        } else {
          if (aPost.comments > 0) {
            dta.comments.data.forEach(function (dtc, index, array) {
              let cPost = {
                from: dtc.from ? dtc.from : { name: "Unknown", id: "9999999999999999" },
                id: dtc.id,
                message: dtc.message,
                updated_time: dtc.created_time,
                comments: dtc.comment_count,
                reactions: dtc.like_count,
                link: "https://www.facebook.com/groups/cryptics/posts/" + dta.id.substr(16) + "/",
                cftd: cftdPost,
                solve: localStorage.getItem(dtc.id) ? JSON.parse(localStorage.getItem(dtc.id))
                  : { ans: "", anno: "", solved: false },
              };
              let selectComment = regexTopic.test(cPost.message) || regexTopic.test(cPost.from.name);
              if (selectComment) {
                console.log(`Selected ${selectCount++}`);
                postsCopy.data.push(aPost);
                postsCopy.data.push(cPost);
              }

            });
          }

        }

      };

    })

    postsCopy.first = false;
    postsCopy.hasMore = (response.data.length === 0) ? false : true;
    postsCopy.nextPage = response.paging ? response.paging.next.substr(33) : "";
    setPosts(postsCopy);
  };
   


  const fetchMoreData = () => {
    console.log('in fetchMoreData');
    if (posts.hasMore) {
      window.FB.api(posts.nextPage, addPosts);
      // if (posts.data.length === feedCount) {fetchMoreData()};
    }
  };

  const handleAnsChange = (idx, key, val) => {
    //console.log(idx +' ' + key + ' ' + val)
    if (!posts.data[idx].solve.solved){
      let postsCopy = Object.assign({}, posts);
      postsCopy.data[idx].solve[key] = val
      // save to localStorage
      const jsonObj = JSON.stringify(postsCopy.data[idx].solve);
      localStorage.setItem(postsCopy.data[idx].id, jsonObj);
      // update state
      setPosts(postsCopy);
    }
  };

  const handleChangeTopic = (e) => {
    setSearchTopic(e.target.value);
    console.log(searchTopic);
  };

  const handleRefreshSearch = (e) => {
    setPosts({
      data: [],
      hasMore: true,
      nextPage: "first",
      first: true
      });
  }

  const handlePost = (idx, event) => {
    if (posts.data[idx].solve.ans){
      setAnchorEl(event.currentTarget);
    };

  // Group api does not allow posting a comment
  /* -- start group comment
    const cmnt = "âœ´ " + posts.data[idx].solve.ans + " => " + posts.data[idx].solve.anno;
    const feedEdge = '/' + process.env.REACT_APP_FACEBOOK_GROUP_ID + '/feed';
    const commentEdge = '/' + posts.data[idx].id + '/comments';
    const commentCommentEdge = '/' + posts.data[idx].id + '/comments';
    window.FB.api(
      commentCommentEdge,
      "POST",
      {
          "message": cmnt
      },
      function (response) {
        if (response && !response.error) {
          let postsCopy = Object.assign({}, posts);
          postsCopy.data[idx].solve.solved = true;
          // save to localStorage
          const jsonObj = JSON.stringify(postsCopy.data[idx].solve);
          localStorage.setItem(postsCopy.data[idx].id, jsonObj);
          // update state
          setPosts(postsCopy);
        }
        else {
          alert (response.error.message)
        }
      }
    ); -- end group comment */
  
  };


  const classes = useStyles();
  
  return (

    <div className={classes.root}>
      <Paper className={classes.paper}>
        <Grid container wrap="nowrap" spacing={1}>
          <Grid item>
            <Avatar src={name.Image}></Avatar>
          </Grid>
          <Grid item xs>
            <Typography align="left" variant="subtitle2">Welcome {name.Name}</Typography>
          </Grid>
          <Grid item  >
          <TextField label="Search group"
          id="outlined-size-small"
          defaultValue="Search group"
          variant="outlined"
          size="small"
            value={searchTopic} onChange={handleChangeTopic}  />
           <IconButton  onClick={handleRefreshSearch}>
               <SearchIcon style={{color: "CornflowerBlue"}}  />
           </IconButton>
        </Grid>
        </Grid>
      </Paper>

      <div>
        <InfiniteScroll
          dataLength={posts.data.length}
          next={fetchMoreData}
          hasMore={posts.hasMore}
          loader={<h4>Loading...</h4>}
          endMessage={
            <p style={{ textAlign: "center" }}>
              <b>You have reached the end</b>
            </p>
          }
        >
          {posts.data.map((dta, index) => (
            <Paper className={classes.paper}>
            <Grid container wrap="nowrap" spacing={2} >
              <Grid item>
                <Avatar>{dta.from.name.split(" ").map((n,i,a) => i === 0 || i+1 === a.length ? n[0] : null).join("")}
                </Avatar>
              </Grid>
    
              <Grid item xs={12}>
                <Typography align="left">{dta.from.name}</Typography>
                <Typography align="left"
                >
                  <Link href={dta.link} target="_blank" rel="noopener noreferrer" variant="body2">
                    {dta.updated_time}
                  </Link>
                </Typography>
              </Grid>
             
              <Grid
                item
                container
                direction="column"
                alignItems="flex-end"
                justify="flex-start"
              >
                {dta.cftd &&
                <Chip size="small" label="CFTD" style={{background: "CornflowerBlue", color:"White"}} />
                }
              </Grid>
            </Grid>
    
            <Grid container wrap="nowrap" spacing={2}>
              <Grid item xs>
                <Typography align="left">{dta.message}</Typography>
              </Grid>
            </Grid>

            {dta.full_picture && 
            <grid item>
              <img width="400" height="300" alt="pic" src={dta.full_picture} />
            </grid>

            }
    
            <Grid container wrap="nowrap" spacing={2}>
              <Grid item xs>
                <Typography align="left" variant='body2'>likes {dta.reactions}</Typography>
              </Grid>
    
              <Grid item xs>
                <Typography align="right" variant='body2' gutterBottom="true">
                  Comments {dta.comments}
                </Typography>
              </Grid>
            </Grid>
    
            <Grid container spacing={1} style={{ borderTop: "1px solid grey" }}>
              <Grid container direction="column" item xs={1}>
                <Grid
                  item
                  xs
                  
                  style={{ display: "flex", alignItems: "center" }}
                >
                  <Avatar className={classes.img} src={name.Image}></Avatar>
                </Grid>
              </Grid>
    
              <Grid
                container
                direction="column"
                item
                xs={9}
              
                alignItems="flex-start"
                justifyContents="flex-start"
              >
                 <Grid item justify="left">
       
                <form className={classes.form} noValidate autoComplete="off">
               <TextField fullWidth
               style={{
                disableUnderline: true,
                background: 'linear-gradient(45deg, #eeeeee 30%, #e0e0e0 90%)',
                borderRadius: "30px 30px 30px 30px",
                padding: '0 30px',
                
                
               }}
               InputProps={{
                disableUnderline: true,
               }}
                className={classes.textField}
                 label="Answer"
                 fullwidth
                 multiline
                 maxRows={4}
                 value={posts.data[index].solve.ans}
                 onChange={(e) => handleAnsChange(index, "ans" , e.target.value)}
                /> 

                {/*  <TextField
                    className={classes.textField}
                    label="Anno"
                    multiline
                    maxRows={4}
                    variant="outlined"
                    fullWidth
                    style={{ marginTop: "4px" }}
                    value={posts.data[index].solve.anno}
                    onChange={(e) => handleAnsChange(index, "anno" , e.target.value)}
                    
                  /> */}
                  </form>
                </Grid>
              </Grid>
              <Grid
                container
                direction="column"
                item
                xs={1}
                style={{
                  display: "flex",
                  alignItems: "center",
                  justifyContent: "center"
                }}
              >
                <Grid item>
                  {dta.solve.solved ?
                    <IconButton>
                    <DoneIcon style={{color: "CornflowerBlue"}} />
                  </IconButton>
                  :
                  <IconButton  onClick={(e) => handlePost(index, e)}>
                    <SendIcon style={{color: "CornflowerBlue"}}  />
                  </IconButton> 
                  }
                  <Popover
                  id={id}
                  open={open}
                  anchorEl={anchorEl}
                  onClose={handlePopClose}
                  anchorOrigin={{
                    vertical: 'top',
                    horizontal: 'left',
                  }}
                  transformOrigin={{
                    vertical: 'top',
                    horizontal: 'center',
                  }}
                >
                  <Typography className={classes.typography}>
                    Facebook API doesn't allow posting comments.<br></br>
                    The comment is saved in browser's local storage for reference
                  </Typography>
                </Popover>
                </Grid>
              </Grid>
            </Grid>

            </Paper>
          ))}
        </InfiniteScroll>
      </div>


    </div>
  )
}

export default Dashboard